<!-- eslint-disable vue/no-v-html -->
<template>
  <div>
    <div class="field">
      <div class="control">
        <input
          v-model="subject"
          required
          class="input"
          @input="preview = ''"
        >
      </div>
    </div>

    <div class="field">
      <div class="control">
        <textarea
          v-model="markdown"
          required
          class="textarea"
          @input="preview = ''"
        />
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button
          :disabled="loading"
          :class="{ 'is-loading': loading }"
          type="button"
          class="button is-primary"
          @click="previewSave"
        >
          {{ preview ? 'Save' : 'Preview' }}
        </button>
      </div>
    </div>

    <template v-if="error">
      <div class="notification is-warning has-text-centered">
        Something went wrong.
        <br>
        ({{ error }})
      </div>
    </template>
    <div v-else-if="preview">
      <p>
        <code>Subject: {{ subject }}</code>
      </p>

      <div
        class="content"
        v-html="preview"
      />

      <br>

      <button
        v-if="campaignId && preview"
        :disabled="loading"
        :class="{ 'is-loading': loading }"
        type="button"
        class="button is-success"
        @click="send({ test: true })"
      >
        Send to test subscribers
      </button>

      <br><br><br><br>
      <button
        v-if="campaignId && preview"
        :disabled="loading"
        :class="{ 'is-loading': loading }"
        type="button"
        class="button is-danger is-outlined"
        @click="send({ test: false })"
      >
        Send
      </button>
    </div>
  </div>
</template>

<script>
import { apiRequest } from '../../utils'

const DEFALT_MARKDOWN = '## Enter markdown message'

export default {
  props: {
    campaignId: { type: String, default: '' },
    token: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      loading: false,
      error: false,
      markdown: DEFALT_MARKDOWN,
      preview: '',
      campaign: null,
      subject: ''
    }
  },
  watch: {
    async campaignId (campaignId) {
      this.getCampaign()
    }
  },
  mounted () {
    if (!this.token && !this.__prerendering) {
      this.$router.replace('/')
      return
    }
    this.getCampaign()
  },
  methods: {
    async previewSave () {
      const previewing = !this.preview

      this.error = false
      this.loading = true

      try {
        if (previewing) {
          this.preview = ''
          const { html, error } = await this.campaignRequest('preview')

          if (error) { throw new Error(error) }

          this.preview = html
        } else {
          const { campaign, error } = await this.campaignRequest('save')

          if (error) { throw new Error(error) }

          if (campaign._id !== this.campaignId) {
            this.$router.replace({ path: `/newsletter/compose/${campaign._id}`, query: this.$route.query })
          }
        }
      } catch (error) {
        this.error = error.message
      }

      this.loading = false
    },

    async getCampaign () {
      this.markdown = DEFALT_MARKDOWN
      this.subject = ''
      this.preview = ''
      this.error = null

      if (this.campaignId) {
        this.loading = true

        try {
          const { campaign, error } = await this.campaignRequest('get')

          if (error) { throw new Error(error) }

          this.markdown = campaign.markdown
          this.subject = campaign.subject
          const { html } = await this.campaignRequest('preview')

          this.preview = html
        } catch (error) {
          this.error = error.message
        }

        this.loading = false
      }
    },

    async send ({ test = true }) {
      if (!test && window.prompt('Are you sure you want to send this newsletter out for real? Enter "yes"') !== 'yes') {
        return
      }

      this.loading = true

      const { sent, failed, error } = await this.campaignRequest('send', { test })

      window.alert(`Sent to ${sent} out of ${sent + failed} subscribers. ${error || ''}`)

      this.loading = false
      this.error = error
    },

    campaignRequest (endpoint, data) {
      const { token, markdown, campaignId, subject } = this

      return apiRequest(`newsletter/campaign/${endpoint}`,
        { markdown, campaignId, subject, ...data },
        { headers: { authorization: `Bearer ${token}` } }
      )
    }
  }
}
</script>
